import React, { useState } from "react";
import SpellChecker from "hunspell-spellchecker";

const SpellCheckers = () => {
  const [text, setText] = useState("");
  const spellCheckerr = new SpellChecker();

// بارگذاری دیکشنری فارسی
  const dictionary = spellCheckerr.parse({
    aff: "../assets/img/farsi.aff",
    dic: "../assets/img/farsi.dic",

  });

  spellCheckerr.use(dictionary);
  const getHighlightedText = () => {
    const words = text.split(" ");
    console.log(words);
   if(words?.length){
     console.log(444);
     return words.map((word) => {
       const isCorrect = spellCheckerr.check(word); // بررسی درستی کلمه
       console.log(isCorrect);
       return isCorrect
         ? word
         : `<span style="text-decoration: underline; color: red;">${word}</span>`;
     }).join(" ");
   }
  };

  return (
    <div>
      <textarea
        value={text}
        onChange={(e) => setText(e.target.value)}
        rows={5}
        cols={50}
      />
      <div
        dangerouslySetInnerHTML={{ __html: getHighlightedText() }}
        style={{ marginTop: "20px" }}
      />
    </div>
  );
};

export default SpellCheckers;
